import request from '@/utils/request'

// 
export function getTodos () {
  return request({
    method: 'GET',
    url: '/todos'
  })
}

// 
export function createTodo (todoData) {
  console.log(todoData)
  return request({
    method: 'POST',
    url: '/todos',
    data: todoData
  })
}

// 
export function finishedTodo (id) {
  return request({
    method: 'PUT',
    url: `/todos/finishedTodo/${id}`
  })
}

// 
export function deleteTodo (id) {
  return request({
    method: 'DELETE',
    url: `/todos/${id}`
  })
}

// 
export function updateTodo (todoData) {
  console.log(todoData)
  return request({
    method: 'PUT',
    url: `/todos/${todoData.id}`,
    data: todoData
  })
}

// 
export function getTodo (id) {
  return request({
    method: 'GET',
    url: `/todos/${id}`
  })
}
